import './App.css';
import React, { useEffect, useRef, useState } from 'react';
import {
  BinaryBitmap,
  HybridBinarizer,
  PDF417Reader,
  RGBLuminanceSource
} from '@zxing/library'

function App() {
  const videoRef = useRef(null)
  const canvasRef = useRef(null)
  const actualCanvasRef = useRef(null)
  const [text, setText] = useState("")
  const timer = useRef(null)
  const getRatio = () => {
    return Math.ceil(devicePixelRatio) || 1
  }
  const reader = new PDF417Reader()

  function getBinaryBitmap(image) {
    const len = image.width * image.height
    const lumiArray = new Uint8ClampedArray(len)
    for (let i = 0; i < len; i++) {
      /* eslint-disable no-bitwise */
      lumiArray[i] = ((image.data[i * 4] + image.data[i * 4 + 1] * 2 + image.data[i * 4 + 2]) / 4) & 0xff
    }
    const lumiSource = new RGBLuminanceSource(lumiArray, image.width, image.height)
    const binaryBitmap = new BinaryBitmap(new HybridBinarizer(lumiSource))
    return binaryBitmap
  }

  function checkBarcode(image) {
    try {
      const decoded = reader.decode(getBinaryBitmap(image))
      const result = decoded.getText()
      setText(result)
    } catch (err) {
      console.error(err)
      setText("no barcode detected: " + err)
    }
  }

  function adjustVideoSize(w, h, R) {
    let r = w / h
    let width = w
    let height = h
    if (R > r) {
      width = (R / r) * w
    } else {
      height = (r / R) * h
    }
    return {
      width, height
    }
  }
  function getDefaultVideoRatio(stream) {
    let actualRatio = stream.getTracks()[0].getSettings().aspectRatio
    if (!actualRatio) {
      if (stream.getTracks()[0].getSettings().width > stream.getTracks()[0].getSettings().height) {
        actualRatio = stream.getTracks()[0].getSettings().height / stream.getTracks()[0].getSettings().width
      } else {
        actualRatio = stream.getTracks()[0].getSettings().width / stream.getTracks()[0].getSettings().height
      }
    }
    return actualRatio
  }
  const streamHandler = (stream) => {
    const wrapper = document.getElementById('container')
    let actualVideoSize = {}
    videoRef.current.srcObject = stream
    const loadedmetadata = function () {
      videoRef.current.play()
      const sizeTestVideoWidth = videoRef.current.clientWidth
      const sizeTestVideoHeight = videoRef.current.clientHeight
      let ratio = getDefaultVideoRatio(stream)
      if (sizeTestVideoWidth !== 0 && sizeTestVideoHeight !== 0) {
        ratio = sizeTestVideoWidth / sizeTestVideoHeight
      }
      actualVideoSize = adjustVideoSize(
        wrapper.clientWidth,
        wrapper.clientHeight,
        ratio
      )
      videoRef.current.width = actualVideoSize.width
      videoRef.current.height = actualVideoSize.height
      actualCanvasRef.current.width = actualVideoSize.width * getRatio()
      actualCanvasRef.current.height = actualVideoSize.height * getRatio()
    }
    videoRef.current.addEventListener('loadedmetadata', (event) => {
      loadedmetadata()
    })
  }
  const initialMedia = () => {
    navigator.mediaDevices.getUserMedia({
      video: {
        facingMode: 'environment',
        width: {
          ideal: 1280
        },
      }
    }).then(stream => {
      streamHandler(stream)
    })
  }

  function getVideoOffset() {
    const wrapper = document.getElementById('container')
    const offset = (videoRef.current.videoHeight - wrapper.clientHeight) / 2 + (wrapper.clientHeight - 225) / 2
    return offset
  }

  const getImage = () => {
    const ratio = getRatio()
    const acturalCtx = actualCanvasRef.current.getContext("2d")
    const ctx = canvasRef.current.getContext("2d")
    const aw = actualCanvasRef.current.width
    const ah = actualCanvasRef.current.height
    const ew = 300 * ratio;
    const eh = 225 * ratio;
    acturalCtx.drawImage(videoRef.current, 0, 0, aw, ah)
    const diffx = (aw - ew) / 2
    const diffy = getVideoOffset() * ratio
    canvasRef.current.width = ew * ratio
    canvasRef.current.height = eh * ratio
    ctx.drawImage(actualCanvasRef.current, diffx, diffy, ew, eh, 0, 0, ew * ratio, eh * ratio);
    // ctx.drawImage(videoRef.current, 200, 30, 300, 225, 0, 0)
    // let image = canvasRef.current.toDataURL('image/jpeg');
    canvasRef.current.width = ew
    canvasRef.current.height = eh
    ctx.drawImage(actualCanvasRef.current, diffx, diffy, ew, eh, 0, 0, ew, eh)
    const imageData = ctx.getImageData(0, 0, ew, eh)
    checkBarcode(imageData)
  }
  useEffect(() => {
    initialMedia()
    timer.current = setInterval(() => {
      getImage()
    }, 500)
    return () => {
      clearInterval(timer.current)
    }
  }, [])
  return (
    <div className="App" id="container">
      <div className="border" />
      <video ref={videoRef} className='video' id="video" preload="auto" playsInline />
      <canvas
        ref={canvasRef}
        className="canvas"
      />
      <canvas ref={actualCanvasRef}
        className="canvas"
      />
      <button onClick={getImage} className="button" />
      <div className="result">{text}</div>
    </div>
  );
}

export default App;
